@import url('https://fonts.googleapis.com/css2?family=DotGothic16&family=Open+Sans&family=Roboto&display=swap');

.dots {
    background: url("images/circle.png");
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    position: absolute;
    animation: slide 3s linear infinite;
}

@keyframes slide {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 115px -115px;
    }
}



#background {
    position: absolute;
    width: 100%;
    height: 100%;
    display: inline-block;
    background-color: #8ba1d9;
}

#center-text {
    position: relative;
    font-family: 'DotGothic16', sans-serif;
    color: #e8caca;
    text-shadow: -2px 5px 0 #665b5b;
    font-size: 3.5vw;
    animation: sizeText 0.5s linear;
    animation-fill-mode: forwards;
}

@keyframes sizeText {
    from {
        font-size: 0vw;
    }
    to {
        font-size: 3.5vw;
    }
}

#center-box {
    position: relative;
    width: 35%;
    height: 100px;
    background-color: rgba(21, 20, 17, 0.22);
    display: flex;
    justify-content: center;
    align-items: center;
    animation: wide 0.5s linear;
    border-radius: 25px;
    box-shadow: 0px 0px 150px #ffffff;
}

@keyframes wide {
    from {
        width: 0%;
    }
    to {
        width: 35%;
    }
}

#main {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#vinjette {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    box-shadow: inset 0px 0px 150px #2b2727;
}

#drop {
    position: fixed;
    backgroundSize: cover;
    width: 115px;
    height: 115px;
    zIndex: 1 !important;
    background: url("images/circle2.png");
    animation: dropDown 2s;
    top: 100%;
    animation-fill-mode: forwards;
}

@keyframes dropDown {
    from {
        transform: translateY(0px);
    }
    to {
        transform: translateY(1000px);
    }
}

#playButton{
    position: absolute;
    top:5%;
    left:5%;
    width: 100px;
    height: 100px;
    z-index: 2;
}